.page-header {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 101;
  width: 100%; }

.page-header nav {
  display: block; }

.page-header nav a {
  margin-left: 16px;
  padding: 8px 16px;
  border-radius: 4px;
  font-size: 16px;
  line-height: 22px;
  font-weight: bold;
  text-decoration: none;
  color: #2c3734;
  font-family: Montserrat, tahoma, verdana, sans-serif; }
  .page-header nav a:first-child {
    margin-left: 0; }

.page-header-img {
  height: 5rem;
  width: 5rem; }

.page-header__wrapper {
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin: 0 auto;
  padding: 32px 10%;
  align-items: center; }
  @media all and (max-width: 768px) {
    .page-header__wrapper {
      flex-direction: column;
      align-items: flex-start;
      padding: 12px 24px; }
      .page-header__wrapper nav {
        margin-top: 24px;
        display: flex;
        width: 100%;
        align-items: center;
        justify-content: center; }
        .page-header__wrapper nav a {
          background-color: rgba(0, 0, 0, 0.05);
          margin-left: 10px;
          padding: 6px 12px;
          font-size: 14px;
          line-height: 18px; }
      .page-header__wrapper .page-header__title h1 {
        font-size: 20px;
        line-height: 28px; }
      .page-header__wrapper .logo-container {
        --size: 64px; } }
  @media all and (max-width: 475px) {
    .page-header__wrapper {
      padding: 20px; }
      .page-header__wrapper .page-header__title h1 {
        font-size: 18px;
        line-height: 24px; }
      .page-header__wrapper .page-header__title h2 {
        font-size: 12px;
        line-height: 18px; } }

.page-header small {
  font-size: 8px;
  text-transform: uppercase;
  font-family: Montserrat, tahoma, verdana, sans-serif;
  color: #5e5f5f; }

.page-header small a {
  color: #5e5f5f;
  font-weight: bold; }

.page-header__wrapper > :nth-child(1),
.page-header__wrapper > :nth-child(2) {
  transform: translateY(-175px); }

.animated .page-header__wrapper > :nth-child(1),
.animated .page-header__wrapper > :nth-child(2) {
  transition: transform 335ms cubic-bezier(0.05, 0.55, 0.15, 1); }

.animated .page-header__wrapper > :nth-child(2) {
  transition-delay: 0.125s; }

.page-header__title {
  display: flex;
  align-items: center; }

.page-header__title img {
  size: 20px; }

.page-header__title h1 {
  font-size: 24px;
  line-height: 32px;
  color: #5e5f5f;
  font-family: Montserrat, tahoma, verdana, sans-serif;
  display: block; }

.page-header__title h2 {
  color: #5e5f5f;
  font-family: Montserrat, tahoma, verdana, sans-serif;
  display: block;
  font-size: 14px;
  display: block; }

.page-header__title > div {
  margin-left: 8px; }

.page-header__title h1 span {
  color: #5e5f5f;
  font-family: Montserrat, tahoma, verdana, sans-serif;
  display: inline-block;
  align-self: center; }

.page-header a.selected {
  background-color: rgba(0, 0, 0, 0.1); }

.page-header a:active {
  background-color: rgba(0, 0, 0, 0.05); }

.visible .page-header__wrapper > :nth-child(1),
.visible .page-header__wrapper > :nth-child(2) {
  transform: translateY(0); }
